/* main app navbar */

/* wayne */
.app-block-wayne {
  background-color: #000;
}
.app-block-wayne .row {
  margin-bottom: 75px;
}

/* music grid */
.app-block-grid {
  margin-top: -25px;
}
.app-block-grid .container {
  position: relative;
  top: -75px;
  z-index: 2;
}
.app-block-grid img {
  width: 100%;
}


/* chainz block */
.app-block-chainz {
  z-index: 5;
}
.app-block-chainz .block-bg {
  border-bottom: 10px solid #eee04a;
}


/* kanye block */
.app-block-kanye {
  z-index: 4;
}
.app-block-kanye .block-bg {
  border-bottom: 10px solid #3d252a;
}


/* mosdef block */


/* nicki */
